import React, { memo, Dispatch, SetStateAction } from "react";
import useSetHeader from "../../hooks/useSetHeader";
import useSetQueryWithoutHistory from "../../hooks/useSetQueryWithoutHistory";
import Ruby from "../../components/atoms/Ruby/Ruby";
import _ from "underscore";
import { Mb } from "../../components/atoms/Mb";
import { QuestionTitle } from "../../components/atoms/QuestionTitle/QuestionTitle";
import { CSImage } from "../../components/atoms/CSImage/CSImage";
import { MainTextSourceHanSansJp } from "../../components/atoms/MainTextSourceHanSansJp/MainTextSourceHanSansJp";
import { CountDownBlue } from "../../components/atoms/CountDownBlue/CountDownBlue";
import { ImgWIthCountDown } from "../../components/molecules/ImgWIthCountDown/ImgWIthCountDown";
import useCallRescue from "../../hooks/useCallRescue";

export interface MainProps {
  setPageNum: Dispatch<SetStateAction<number>>;
  pageNum: number;
}

/**
 * Functional component.
 * @param props 
 */
export const Description1: React.FC<MainProps> = memo(({
  setPageNum,
  pageNum,
}) => {

  useSetHeader();
  useSetQueryWithoutHistory(pageNum);

  const count = useCallRescue(setPageNum);

  return (
    <>
      <Mb
        size={ {
          pc: 39,
          tb: 37,
          sp: 27
        } }
      >
        <QuestionTitle icon="simulation_icon.png">
          <Ruby ruby="きゅうじょたい">救助隊</Ruby>に<Ruby ruby="でんわ">電話</Ruby>せよ！
        </QuestionTitle>
      </Mb>
      <Mb
        size={ {
          pc: 65,
          tb: 65,
          sp: 56
        } }
      >
        <CSImage
          desc="海上保安庁"
          maxWidth={ {
            pc: 576,
            tb: 475,
            sp: 203
          } }
        >
          <ImgWIthCountDown
            image="iwami.jpg"
            ratio={ 75.478 }
          >
            <CountDownBlue count={ count }/>
          </ImgWIthCountDown>
        </CSImage>
      </Mb>
      <Mb
        size={ {
          pc: 30,
          tb: 30,
          sp: 30
        } }
      >
        <MainTextSourceHanSansJp
          fontSize={ {
            pc: 24,
            tb: 24,
            sp: 24
          } }
        >
          あなたの<Ruby ruby="なまえ">名前</Ruby>、連絡先を教えてください。直ちに巡視船艇を向かわせます。
        </MainTextSourceHanSansJp>
      </Mb>
    </>
  );
});