import Head from "next/head";
import React from "react";
import useInitTransition from "../../../../hooks/useInitTransition";
import { Start } from "../../../../contents/call/Start";
import { Illust } from "../../../../contents/call/Illust";
import { Call } from "../../../../contents/call/Call";
import { Quiz1 } from "../../../../contents/call/Quiz1";
import { Quiz2 } from "../../../../contents/call/Quiz2";
import { Quiz3 } from "../../../../contents/call/Quiz3";
import { Quiz4 } from "../../../../contents/call/Quiz4";
import { Description1 } from "../../../../contents/call/Description1";
import { Description2 } from "../../../../contents/call/Description2";
import { ResultClear } from "../../../../contents/call/ResultClear";
import { ResultFailed } from "../../../../contents/call/ResultFailed";
import useSwitchPage from "../../../../hooks/useSwitchPage";
import useSetHeader from "../../../../hooks/useSetHeader";
import { Content } from "../../../../components/templates/Content/Content";

export default function Page({
  setTransTime
}) {

  useSetHeader();
  useInitTransition(setTransTime);

  const content = useSwitchPage((setPageNum, pageNum) => {
    return [
      // <Start setPageNum={ setPageNum } />,
      // <Illust setPageNum={ setPageNum } pageNum={ pageNum } />,
      // <Call setPageNum={ setPageNum } pageNum={ pageNum } />,
      // <Quiz1 setPageNum={ setPageNum } pageNum={ pageNum } />,
      // <Quiz2 setPageNum={ setPageNum } pageNum={ pageNum } />,
      // <Quiz3 setPageNum={ setPageNum } pageNum={ pageNum } />,
      // <Quiz4 setPageNum={ setPageNum } pageNum={ pageNum } />,
      <Description1 setPageNum={ setPageNum } pageNum={ pageNum } />,
      // <Description2 setPageNum={ setPageNum } pageNum={ pageNum } />,
      <ResultClear pageNum={ pageNum } />,
      <ResultFailed setPageNum={ setPageNum } pageNum={ pageNum } />
    ];
  });

  return (
    <>
      <Head>
        <title>救助隊に電話せよ！</title>
      </Head>
      <Content>{ content }</Content>
    </>
  );
}
